import { domService } from "../modules/text-document/services/DomService";

export interface DomObserverTargetData {
    target: Node;
    options: MutationObserverInit;
}

export interface DomObserverOptions {
    filterMutationsTargetClass?: string;
    ignoreFilterMutationsTargetAttributes?: boolean;
}

export interface DomObserverHiddenDataOptions {
    hiddenDataContainerClass: string;
}

export type DomObserverCallback = (
    mutations: MutationRecord[],
    observer: MutationObserver,
    hiddenDataElements: HTMLElement[]
) => void;

export class DomObserver {
    static readonly MutationObserverInit_All: MutationObserverInit = {
        attributes: true,
        attributeOldValue: true,
        childList: true,
        subtree: true,
        characterData: true,
        characterDataOldValue: true
    };

    observer: MutationObserver = null as any;

    constructor(
        public targetData: DomObserverTargetData,
        public options: DomObserverOptions,
        // Null if hidden data turned off
        public hiddenDataOptions: DomObserverHiddenDataOptions | null,
        public callback: DomObserverCallback
    ) {
        this.init();
    }

    init() {
        this.initObserver();
    }

    destroy() {
        this.destroyObserver();
    }

    initObserver() {
        this.observer = new MutationObserver(this.onCallback);
        this.observe();
    }

    destroyObserver() {
        this.observer.disconnect();
    }

    static getTarget(initialTarget: Node, options: DomObserverOptions): Node {
        return options.filterMutationsTargetClass
            ? (initialTarget as HTMLElement).querySelector(`.${options.filterMutationsTargetClass}`)!
            : initialTarget;
    }

    getTarget() {
        return DomObserver.getTarget(this.targetData.target, this.options);
    }

    pauseObserving() {
        this.observer.disconnect();
    }

    unpauseObserving() {
        this.observe();
    }

    observe() {
        this.observer.observe(this.targetData.target, this.targetData.options);
    }

    onCallback = (mutations: MutationRecord[], observer: MutationObserver) => {
        // Hidden data elements

        let hiddenDataElements: HTMLElement[] | null = null;

        if (this.hiddenDataOptions) {
            const addHiddenDataMutations = (mutations || []).filter(
                mutationItem => {
                    return mutationItem.type === 'childList'
                        && domService.isHTMLElement(mutationItem.target)
                        && mutationItem.target.classList.contains(this.hiddenDataOptions!.hiddenDataContainerClass)
                        && mutationItem.addedNodes
                        && !!mutationItem.addedNodes.length;
                }
            );

            hiddenDataElements = ([] as HTMLElement[]).concat(
                ...addHiddenDataMutations.map(mutationItem => Array.from(mutationItem.addedNodes) as HTMLElement[])
            );
        }

        hiddenDataElements = hiddenDataElements || [];

        // Filtered mutations

        const filteredMutations = !this.options.filterMutationsTargetClass
            ? mutations
            :
                mutations.filter(mutationItem => {
                    const mutationTargetIsNode =
                        domService.isNode(mutationItem.target)
                        && !domService.isHTMLElement(mutationItem.target);

                    return !!(
                        !!(
                            mutationTargetIsNode
                                ? mutationItem.target.parentElement
                                : mutationItem.target as HTMLElement
                        )?.closest(`.${this.options.filterMutationsTargetClass}`)
                        && (
                            !this.options.ignoreFilterMutationsTargetAttributes
                                ? true
                                : !(
                                    mutationItem.type === 'attributes'
                                    && (mutationItem.target as HTMLElement).classList.contains(this.options.filterMutationsTargetClass!)
                                )
                        )
                    );
                });

        // Invoke callback

        if (filteredMutations.length) {
            this.callback(filteredMutations, observer, hiddenDataElements);
        }
    };
}