import React from 'react';
import styles from './TextDocument.module.scss';

import FroalaEditor from 'react-froala-wysiwyg';
import { EditorLogic } from '../../editor-logic/EditorLogic';
import { domService } from '../../services/DomService';
import { testModels } from '../../test-models/test-models';


class TextDocument extends React.Component {
  private logic = new EditorLogic();

  state = {
    // TODO: Remove model and set editor html using DOM.
    // Remove model with text search
    model: null
  };

  config = {};

  // TODO: Remove
  handleModelChange = (model: string | object | null): void => {
    this.setState({
      ...this.state,
      model
    });
  };

  setFroalaEditorRef(ref: FroalaEditor | null) {
    if (!ref) {
      return;
    }

    this.logic.setFroalaEditorRef(ref);
  }

  openOnlyTextDocument = () => {
    this.logic.editorModel.setInitialModel(testModels.onlyTextModel);
  };

  render() {
    return (
      <div className={styles.TextDocument}>
        <div>
          Documents:
        </div>
        <br />
        <div>
          <button type="button" onClick={this.openOnlyTextDocument}>Only Text Document</button>
        </div>
        <br />
        <FroalaEditor
          ref={(ref) => (this.setFroalaEditorRef(ref))}
          tag='textarea'
          config={this.config}
          model={this.state.model}
          onModelChange={this.handleModelChange}
        />
      </div>
    );
  }
}

export default TextDocument;
