export type Writable<T> = { -readonly [P in keyof T]: T[P] };

export type DeepWritable<T> = { -readonly [P in keyof T]: DeepWritable<T[P]> };

export class AddableArrayValueMap<TKey, TArrayItem> extends Map<TKey, TArrayItem[]> {
    addArrayItem(key: TKey, item: TArrayItem) {
        if (!this.has(key)) {
            this.set(key, []);
        }

        this.get(key)!.push(item);
    }
}

export interface NodeWithIndicesByElementId extends Node {
    indicesByElementId: Map<string, Node>;
}

export interface TRBL {
    top: number;
    right: number;
    bottom: number;
    left: number;
}

export const ZeroTRBL: TRBL = {
    top: 0,
    right: 0,
    bottom: 0,
    left: 0
};

export interface Position {
    left: number;
    top: number;
}