import { DomObserver, DomObserverHiddenDataOptions, DomObserverOptions } from "./DomObserver";
import { domService } from "../modules/text-document/services/DomService";
import { guidValues } from "./GuidValues";
import { elementIdGenerator } from "./ElementIdGenerator";
import { AddableArrayValueMap, NodeWithIndicesByElementId, Writable } from "./BaseTypes";
import { assertService } from "../modules/text-document/services/AssertService";
import { selectionService } from "./SelectionService";

export interface DomMirrorHiddenDataOptions extends DomObserverHiddenDataOptions {
    ignoreIfHiddenDataHasClass: string;
}

class FromNodeToNodes {
    constructor(
        public fromNode: Node | undefined = undefined,
        public toNodes: Node[] = [],

        public rootFromNode: Node | undefined = undefined,
        public rootToNodes: Node[] = []
    ) {}

    iterateNodesRecursively(
        callback: (fromNode: Node, toNodes: Node[]) => void
    ) {
        if (this.fromNode) {
            this.iterateNodesRecursivelyInternal(this.fromNode, this.toNodes, callback);
        }
    }

    iterateNodesRecursivelyInternal(
        fromNode: Node,
        toNodes: Node[],
        callback: (fromNode: Node, toNodes: Node[]) => void
    ) {
        callback(fromNode, toNodes);

        for (let i = 0; i < fromNode.childNodes.length; i++) {
            this.iterateNodesRecursivelyInternal(fromNode.childNodes[i], toNodes.map(node => node.childNodes[i]), callback);
        }
    }
}

class MutationRecordSyncNodes {
    addedNodes: FromNodeToNodes[] = [];
    removedNodes: FromNodeToNodes[] = [];
}

export class DomMirror {
    static readonly IgnorePagesSyncHiddenDataClass = 'ignore-pages-sync__1f405620-556b-4768-95cb-b3288f8bb062';

    observer: DomObserver;

    constructor(
        public fromNode: Node,
        public toNodes: Node[],
        public domObserverOptions: DomObserverOptions,
        // Null if hidden data turned off
        public hiddenDataOptions: DomMirrorHiddenDataOptions | null
    ) {
        this.init();
    }

    init() {}

    onAddToDom() {
        setTimeout(() => {
            setTimeout(() => {
                const rootNodes = [this.fromNode, ...this.toNodes];
                for (const curRootNode of rootNodes) {
                    if (!(curRootNode as NodeWithIndicesByElementId).indicesByElementId) {
                        (curRootNode as NodeWithIndicesByElementId).indicesByElementId = new (
                            class extends Map<string, Node> {
                                delete(key: string): boolean {
                                    return super.delete(key);
                                }
                            }
                        )();
                    }
                }

                const addedNodes = new FromNodeToNodes(
                    DomObserver.getTarget(this.fromNode, this.domObserverOptions),
                    this.toNodes.map(toNode => DomObserver.getTarget(toNode, this.domObserverOptions))
                );
                const removedNodes = null;

                setTimeout(() => {
                    setTimeout(() => {
                        setTimeout(() => {
                            this.updateElementIds(addedNodes, removedNodes);

                            try {
                                const n = [this.fromNode, ...this.toNodes].map(n => DomObserver.getTarget(n, this.domObserverOptions));
                                new FromNodeToNodes(n[0], n.slice(1)).iterateNodesRecursively((f, ts) => {
                                    for (const t of ts) {
                                        assertService.assert(!!(f as any)[guidValues.elementIdAttributeName]);
                                        assertService.assert((t as any)[guidValues.elementIdAttributeName] === (f as any)[guidValues.elementIdAttributeName]);
                                    }
                                });
                            } catch (e) {
                                console.log('--it');
                                console.error(e);
                            }
                    
                            try {
                                const f = DomObserver.getTarget(this.fromNode, this.domObserverOptions);
                                const t = DomObserver.getTarget(this.toNodes[0], this.domObserverOptions);
                    
                                assertService.assert(
                                    (f as any).innerHTML === (t as any).innerHTML
                                );
                            } catch (e) {
                                console.log('--ih');
                                console.error(e);
                            }

                            this.initObserver();
                        }, 1000 * 1);
                    });
                });
            });
        });
    }

    destroy() {
        this.destroyObserver();
    }

    initObserver() {
        this.observer = new DomObserver(
            {
                target: this.fromNode,
                options: DomObserver.MutationObserverInit_All
            },
            this.domObserverOptions,
            this.hiddenDataOptions,
            this.observerCallback
        );
    }

    destroyObserver() {
        this.observer.destroy();
    }

    observerCallback = (
        mutations: MutationRecord[],
        observer: MutationObserver,
        hiddenDataElements: HTMLElement[]
    ) => {
        if (this.hiddenDataOptions && this.hasIgnoreHiddenDataElement(hiddenDataElements)) {
            return;
        }

        const selectionData = selectionService.getSelectionData();

        this.observer.pauseObserving();
        this.applyChildListMutations(this.createReverseChildListMutations(mutations));
        this.syncMutations(mutations);
        this.observer.unpauseObserving();

        this.addIgnoreHiddenDataElementInToNodes();

        selectionService.setSelectionData(selectionData);
    };

    hasIgnoreHiddenDataElement(hiddenDataElements: HTMLElement[]): boolean {
        let result = false;

        for (const element of hiddenDataElements) {
            if (element.classList.contains(this.hiddenDataOptions!.ignoreIfHiddenDataHasClass)) {
                result = true;
                break;
            }
        }

        return result;
    }

    createReverseChildListMutations(mutations: MutationRecord[]): MutationRecord[] {
        return mutations
            .slice()
            .reverse()
            .map(mutationItem => {
                const resultMutationItem: Writable<MutationRecord> = {
                    addedNodes: mutationItem.addedNodes,
                    attributeName: mutationItem.attributeName,
                    attributeNamespace: mutationItem.attributeNamespace,
                    nextSibling: mutationItem.nextSibling,
                    oldValue: mutationItem.oldValue,
                    previousSibling: mutationItem.previousSibling,
                    removedNodes: mutationItem.removedNodes,
                    target: mutationItem.target,
                    type: mutationItem.type
                };

                const temp = resultMutationItem.addedNodes;
                resultMutationItem.addedNodes = resultMutationItem.removedNodes;
                resultMutationItem.removedNodes = temp;

                return resultMutationItem;
            });
    }

    applyChildListMutations(mutations: MutationRecord[]) {
        for (const mutationItem of mutations) {
            if (mutationItem.type === 'childList') {
                this.applyChildListMutationRecord(mutationItem);
            }
        }
    }

    applyChildListMutationRecord(
        mutationItem: {
            type: MutationRecordType,
            target: Node,
            addedNodes: ArrayLike<Node>,
            removedNodes: ArrayLike<Node>,
            previousSibling: Node | null,
            nextSibling: Node | null
        },
        mode: 'addedNodes' | 'removedNodes' | 'allNodes' = 'allNodes'
    ) {
        assertService.assert(mutationItem.type === 'childList');

        if (['allNodes', 'removedNodes'].includes(mode)) {
            const removingNodes = this.filterRemovedNodesForSync(mutationItem.target, mutationItem.removedNodes);
            const hasRemovedNodes = removingNodes?.length;

            if (hasRemovedNodes) {
                Array.prototype.forEach.call(removingNodes, node => mutationItem.target.removeChild(node));
            }
        }

        if (['allNodes', 'addedNodes'].includes(mode)) {
            const addingNodes = this.filterAddedNodesForSync(mutationItem.target, mutationItem.addedNodes);
            const hasAddedNodes = addingNodes?.length;

            if (hasAddedNodes) {
                if (!mutationItem.nextSibling) {
                    Array.prototype.forEach.call(addingNodes, node => mutationItem.target.appendChild(node));
                } else {
                    Array.prototype.forEach.call(addingNodes, node => mutationItem.target.insertBefore(node, mutationItem.nextSibling));
                }
            }
        }
    }

    syncMutations(mutations: MutationRecord[]) {
        /*
        if (mutations.map(m => m.target.parentElement && m.target.parentElement.closest('.page-container')).filter(e => e?.id === 'page-component-2').length) {
            console.log(
                mutations.map(m => m.target.parentElement && m.target.parentElement.closest('.page-container')).filter(e => e?.id === 'page-component-2')
            );
            debugger
        }
        */

        // TODO: Remove
        const syncNodes = new Map<MutationRecord, MutationRecordSyncNodes>();

        for (const mutationItem of mutations) {
            const mutationRecordSyncNodes = new MutationRecordSyncNodes();
            syncNodes.set(mutationItem, mutationRecordSyncNodes);

            const toTargets = this.toNodes.map(
                toNode => {
                    return (toNode as NodeWithIndicesByElementId).indicesByElementId.get((mutationItem.target as any)[guidValues.elementIdAttributeName])!;
                }
            );

            if (mutationItem.type === 'attributes') {
                for (const toTarget of toTargets) {
                    const fromTargetElement = mutationItem.target as HTMLElement;
                    const toTargetElement = toTarget as HTMLElement;

                    const attrName = mutationItem.attributeName!;
                    const attrNamespace = mutationItem.attributeNamespace;

                    if (!attrNamespace) {
                        if (!fromTargetElement.hasAttribute(attrName)) {
                            toTargetElement.removeAttribute(attrName);
                        } else {
                            toTargetElement.setAttribute(attrName, fromTargetElement.getAttribute(attrName)!);
                        }
                    } else {
                        if (!fromTargetElement.hasAttributeNS(attrNamespace, attrName)) {
                            toTargetElement.removeAttributeNS(attrNamespace, attrName);
                        } else {
                            toTargetElement.setAttributeNS(attrNamespace, attrName, fromTargetElement.getAttributeNS(attrNamespace, attrName)!);
                        }
                    }
                }
            } else if (mutationItem.type === 'characterData') {
                for (const toTarget of toTargets) {
                    toTarget.nodeValue = mutationItem.target.nodeValue;
                }
            } else if (mutationItem.type === 'childList') {
                const removingNodes = this.filterRemovedNodesForSync(mutationItem.target, mutationItem.removedNodes);
                const addingNodes = this.filterAddedNodesForSync(mutationItem.target, mutationItem.addedNodes);
                mutationRecordSyncNodes.addedNodes = Array(addingNodes.length).fill(null).map(() => new FromNodeToNodes());
                mutationRecordSyncNodes.removedNodes = Array(removingNodes.length).fill(null).map(() => new FromNodeToNodes());

                removingNodes.forEach((n, i) => {
                    mutationRecordSyncNodes.removedNodes[i].fromNode = mutationRecordSyncNodes.removedNodes[i].fromNode || n;
                    mutationRecordSyncNodes.removedNodes[i].rootFromNode = mutationRecordSyncNodes.removedNodes[i].rootFromNode || this.fromNode;
                });

                toTargets.forEach((toTarget, i) => {
                    const toNode = this.toNodes[i];

                    const hasRemovedNodes = removingNodes?.length;

                    if (hasRemovedNodes) {
                        const nextSiblingIndex = !mutationItem.nextSibling
                            ? null
                            : Array.prototype.indexOf.call(mutationItem.target.childNodes, mutationItem.nextSibling);

                        const nextSiblingIndexForRemove = !mutationItem.nextSibling
                            ? toTarget.childNodes.length - 1
                            : nextSiblingIndex! - 1;

                        for (
                            let
                                i = 0,
                                removeIndex = nextSiblingIndexForRemove;
                            i < removingNodes.length;
                            i++, removeIndex--
                        ) {
                            const nodeToRemove = (toNode as NodeWithIndicesByElementId).indicesByElementId.get((removingNodes[i] as any)[guidValues.elementIdAttributeName]);
                                //toTarget.childNodes[removeIndex];

                            if (nodeToRemove) {
                                mutationRecordSyncNodes.removedNodes[i].toNodes.push(nodeToRemove);
                                mutationRecordSyncNodes.removedNodes[i].rootToNodes.push(toNode);

                                toTarget.removeChild(nodeToRemove);
                            }
                        }
                    }
                });

                this.applyChildListMutationRecord(
                    {
                        type: mutationItem.type,
                        target: mutationItem.target,
                        addedNodes: addingNodes,
                        removedNodes: removingNodes,
                        previousSibling: mutationItem.previousSibling,
                        nextSibling: mutationItem.nextSibling
                    },
                    'removedNodes'
                );

                for (const toTarget of toTargets) {
                    const hasAddedNodes = addingNodes?.length;

                    if (hasAddedNodes) {
                        const nextSiblingIndex = !mutationItem.nextSibling
                            ? null
                            : Array.prototype.indexOf.call(mutationItem.target.childNodes, mutationItem.nextSibling);

                        const toNextSibling = !mutationItem.nextSibling
                            ? null
                            : toTarget.childNodes[nextSiblingIndex!];

                        for (let i = 0; i < addingNodes.length; i++) {
                            const node = addingNodes[i];
                            const clonedNode = node.cloneNode(true);

                            mutationRecordSyncNodes.addedNodes[i].fromNode = mutationRecordSyncNodes.addedNodes[i].fromNode || node;
                            mutationRecordSyncNodes.addedNodes[i].toNodes.push(clonedNode);

                            if (!toNextSibling) {
                                toTarget.appendChild(clonedNode);
                            } else {
                                toTarget.insertBefore(clonedNode, toNextSibling);
                            }
                        }
                    }
                }

                this.applyChildListMutationRecord(
                    {
                        type: mutationItem.type,
                        target: mutationItem.target,
                        addedNodes: addingNodes,
                        removedNodes: removingNodes,
                        previousSibling: mutationItem.previousSibling,
                        nextSibling: mutationItem.nextSibling
                    },
                    'addedNodes'
                );
            }

            // Update element ids

            mutationRecordSyncNodes.addedNodes.forEach(ft => this.updateElementIds(ft, null));

            // TODO: 1
            //mutationRecordSyncNodes.removedNodes.forEach(ft => this.updateElementIds(null, ft));
        }

        for (const mutationItem of mutations) {
            const mutationRecordSyncNodes = syncNodes.get(mutationItem)!;

            // TODO: 1
            mutationRecordSyncNodes.removedNodes.forEach(ft => this.updateElementIds(null, ft));
        }

        const elems = document.querySelectorAll('[id^="mceResizeHandle"]');
        const parents = Array.prototype.map.call(elems, e => e.parentNode) as Node[];
        //
        const attrElems = document.querySelectorAll('[data-mce-selected]');
        type A = {
            elem: Element,
            attrName: string,
            attrValue: string
        };
        const attrs = ([] as A[]).concat(
            ...Array.prototype.map.call(attrElems, (e: Element) => {
                const attrNames = e.getAttributeNames();
                return attrNames
                    .filter(n => n && n.startsWith('data-mce-'))
                    .map(n => {
                        return {
                            elem: e,
                            attrName: n,
                            attrValue: e.getAttribute(n)
                        };
                    });
            }) as A[][]
        );
        //
        this.observer.pauseObserving();
        elems.forEach(e => e.remove());
        attrs.forEach(a => a.elem.removeAttribute(a.attrName));
        try {
            const n = [this.fromNode, ...this.toNodes].map(n => DomObserver.getTarget(n, this.domObserverOptions));
            new FromNodeToNodes(n[0], n.slice(1)).iterateNodesRecursively((f, ts) => {
                if (this) {}
                for (const t of ts) {
                    assertService.assert(!!(f as any)[guidValues.elementIdAttributeName]);
                    assertService.assert((t as any)[guidValues.elementIdAttributeName] === (f as any)[guidValues.elementIdAttributeName]);
                }
            });
        } catch (e) {
            console.log('--it');
            console.error(e);
        }
        try {
            const f = DomObserver.getTarget(this.fromNode, this.domObserverOptions);
            const t = DomObserver.getTarget(this.toNodes[0], this.domObserverOptions);
            assertService.assert(
                (f as any).innerHTML === (t as any).innerHTML
            );
        } catch (e) {
            console.log('--ih');
            console.error(e);
        }
        elems.forEach((e, i) => parents[i].appendChild(e));
        attrs.forEach(a => a.elem.setAttribute(a.attrName, a.attrValue));
        this.observer.unpauseObserving();
    }

    updateElementIds(addedNodes: FromNodeToNodes | null, removedNodes: FromNodeToNodes | null, mutations: MutationRecord[] | null = null) {
        if (addedNodes) {
            addedNodes.iterateNodesRecursively((fromNode, toNodes) => {
                let elementId: string;
                if (!(fromNode as any)[guidValues.elementIdAttributeName]) {
                    elementId = elementIdGenerator.getNewId();
                } else {
                    elementId = (fromNode as any)[guidValues.elementIdAttributeName];
                }


                const rootNodes = [this.fromNode, ...this.toNodes];
                const iteratedNodes = [fromNode, ...toNodes];

                for (let i = 0; i < rootNodes.length; i++) {
                    const curRootNode = rootNodes[i];
                    const curIteratedNode = iteratedNodes[i];

                    (curIteratedNode as any)[guidValues.elementIdAttributeName] = elementId;
                    (curRootNode as NodeWithIndicesByElementId).indicesByElementId.set(elementId, curIteratedNode);
                }
            });
        }

        if (removedNodes) {
            // TODO: 1
            const iterateNodesRecursively = (
                node: Node,
                callback: (node: Node) => void
            ) => {
                callback(node);
        
                for (let i = 0; i < node.childNodes.length; i++) {
                    iterateNodesRecursively(node.childNodes[i], callback);
                }
            };

            const rootNodes = [removedNodes.rootFromNode!, ...removedNodes.rootToNodes];
            const iteratedNodes = [removedNodes.fromNode!, ...removedNodes.toNodes];

            for (let i = 0; i < rootNodes.length; i++) {
                const curRootNode = rootNodes[i];
                const curIteratedNode = iteratedNodes[i];

                let topLevelNode: Node = curIteratedNode;
                while (topLevelNode.parentNode) {
                    topLevelNode = topLevelNode.parentNode;
                }

                if (topLevelNode !== document) {
                    iterateNodesRecursively(curIteratedNode, node => {
                        /*
                        if ((node as any)[guidValues.elementIdAttributeName] === '6') {
                            debugger
                            if (topLevelNode) {}
                        }
                        */
                        if (
                            (curRootNode as NodeWithIndicesByElementId).indicesByElementId.get(
                                (node as any)[guidValues.elementIdAttributeName]
                            ) === node
                        ) {
                            (curRootNode as NodeWithIndicesByElementId).indicesByElementId.delete((node as any)[guidValues.elementIdAttributeName]);
                        }
                        delete (node as any)[guidValues.elementIdAttributeName];
                    });
                }
            }
        }
    }

    addIgnoreHiddenDataElementInToNodes() {
        for (const toNode of this.toNodes) {
            if (this.hiddenDataOptions) {
                const hiddenDataContainer = (toNode as HTMLElement).querySelector(`.${this.hiddenDataOptions.hiddenDataContainerClass}`)!;

                const ignoreHiddenDataElement = document.createElement('div');
                ignoreHiddenDataElement.classList.add(this.hiddenDataOptions.ignoreIfHiddenDataHasClass);

                hiddenDataContainer.appendChild(ignoreHiddenDataElement);
            }
        }
    }

    filterAddedRemovedNodesForSync(target: Node, nodes: ArrayLike<Node>): Node[] {
        return Array.from(nodes);
    }

    filterAddedNodesForSync(target: Node, nodes: ArrayLike<Node>): Node[] {
        return this.filterAddedRemovedNodesForSync(target, nodes);
    }

    filterRemovedNodesForSync(target: Node, nodes: ArrayLike<Node>): Node[] {
        return this.filterAddedRemovedNodesForSync(target, nodes);
    }
}