export interface SelectionData {
    anchorNode: Node | null;
    anchorOffset: number;
    focusNode: Node | null;
    focusOffset: number;
}

class SelectionService {
    getSelectionData(): SelectionData | null {
        const selection = window.getSelection();

        const selectionData: SelectionData | null = !selection
            ? null
            : {
                anchorNode: selection!.anchorNode,
                anchorOffset: selection!.anchorOffset,
                focusNode: selection!.focusNode,
                focusOffset: selection!.focusOffset
            };

        return selectionData;
    }

    setSelectionData(selectionData: SelectionData | null) {
        const selection = window.getSelection();
        if (!selection) {
            return;
        }

        if (!selectionData || !selectionData.anchorNode || !selectionData.focusNode) {
            selection.removeAllRanges();
        } else {
            selection.setBaseAndExtent(
                selectionData.anchorNode,
                selectionData.anchorOffset,
                selectionData.focusNode,
                selectionData.focusOffset
            );
        }
    }
}

export const selectionService = new SelectionService();
