import { Html } from '../models/Model';

export class DomService {
  parseHtml(html: string): Html {
    const div = document.createElement('div');
    div.innerHTML = html;
    return div.childNodes;
  }

  addHtmlTo(html: Html | HTMLElement, parent: HTMLElement) {
    if (this.isHTMLElement(html)) {
      parent.appendChild(html);
    } else {
      for (let i = 0; i < html.length; i++) {
        parent.appendChild(html[i]);
      }
    }
  }

  cloneHtml(html: Html): Html {
    const clone: Html = new Array(html.length);

    for (let i = 0; i < html.length; i++) {
      clone[i] = html[i].cloneNode(true) as ChildNode;
    }

    return clone;
  }

  isNode(o: any): o is Node {
    return (
      typeof Node === "object" ? o instanceof Node : 
      o && typeof o === "object" && typeof o.nodeType === "number" && typeof o.nodeName==="string"
    );
  }

  isHTMLElement(o: any): o is HTMLElement {
    return (
      typeof HTMLElement === "object" ? o instanceof HTMLElement : //DOM2
      o && typeof o === "object" && o !== null && o.nodeType === 1 && typeof o.nodeName==="string"
    );
  }

  getFirstNodeInRootForNode(node: Node, rootNode: Node): Node | null {
    const chain = this.getChainFromNodeToRoot(node, rootNode);
    return chain ? chain[1] : null;
  }

  getAddressForNodeRelativeToRoot(node: Node, rootNode: Node): number[] | null {
    const chain = this.getChainFromNodeToRoot(node, rootNode);
    if (!chain) {
      return null;
    }

    const address: number[] = [];

    for (let i = 1; i < chain.length; i++) {
      const node = chain[i];
      const nodeIndex = Array.prototype.indexOf.call(node.parentNode?.childNodes, node);
      address.push(nodeIndex);
    }

    return address;
  }

  getChainFromNodeToRoot(node: Node, rootNode: Node): Node[] | null {
    const chain: Node[] = [];

    let curNode = node;
    chain.push(curNode);

    while (curNode.parentNode && curNode.parentNode !== rootNode) {
      curNode = curNode.parentNode;
      chain.push(curNode);
    }

    if (curNode.parentNode) {
      chain.push(curNode.parentNode);
    }

    return curNode.parentNode ? chain.reverse() : null;
  }

  getNodeByAddressRelativeToRoot(address: number[], rootNode: Node): Node {
    let curNode = rootNode;

    for (const index of address) {
      curNode = curNode.childNodes[index];
    }

    return curNode;
  }

  getClosestElement(node: Node): Element | null {
    let curNode: Node | null = node;

    do {
      if (this.isHTMLElement(curNode)) {
        return curNode as Element;
      }
    } while (!!(curNode = curNode.parentNode));

    return null;
  }
}

export const domService = new DomService();
