import { assertService } from "../modules/text-document/services/AssertService";

export interface Styles {
    [styleName: string]: string;
}

export class ElementStyles {
    styles: Styles;

    constructor(
        public element: HTMLElement
    ) {
        assertService.assert(!!element);
    }

    set(styles: Styles) {
        if (this.styles) {
            for (const styleName in this.styles) {
                (this.element.style as any)[styleName] = '';
            }
        }

        if (styles) {
            for (const styleName in styles) {
                (this.element.style as any)[styleName] = styles[styleName];
            }
        }

        this.styles = styles;
    }
}