import { domService } from "../services/DomService";

export type Html = NodeListOf<ChildNode> | ChildNode[];

export interface HOrFBase {
  html: Html | null;
}

export interface H extends HOrFBase {}

export interface F extends HOrFBase {}

export interface HF {
  h: H;
  f: F;
}

export interface ContentLine {
  html: Html;
}

export class Model {
  contentLine: ContentLine;
  hfs: HF[];

  parent: HTMLElement | null;

  constructor(
    contentLine: ContentLine,
    hfs: HF[]
  ) {
    this.contentLine = contentLine;
    this.hfs = hfs;

    this.parent = null;
  }

  setTo(parent: HTMLElement) {
    parent.innerHTML = '';
    domService.addHtmlTo(this.contentLine.html, parent);
    this.parent = parent;
  }

  cloneWithoutParent(): Model {
    const contentLine = {
      html: domService.cloneHtml(this.contentLine.html)
    };
    const hfs: HF[] = [];

    return new Model(contentLine, hfs);
  }

  cloneTo(parent: HTMLElement): Model {
    const clone = this.cloneWithoutParent();
    clone.setTo(parent);
    return clone;
  }
}