import { PageComponent } from "./PageComponent";
import { domService } from "../modules/text-document/services/DomService";
import { Position, TRBL } from "./BaseTypes";
import { Html } from "../modules/text-document/models/Model";
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { assertService } from "../modules/text-document/services/AssertService";
import { DomMirror, DomMirrorHiddenDataOptions } from "./DomMirror";
import { DomObserver, DomObserverOptions } from "./DomObserver";
import SmoothScrollbar from 'smooth-scrollbar';
import { Scrollbar } from "smooth-scrollbar/scrollbar";
import { QuillMirror } from "./QuillMirror";

type SelectionRect = {
    left: number;
    top: number;
    width: number;
    height: number;
};

enum PageComponentPinMode {
    hasCaret = 'hasCaret'
}

interface PageComponentInfo {
    pinMode: PageComponentPinMode | null,
    // TODO: Remove PageComponent.isPageIndexUnset
    isVisible: boolean;
}

interface GetNearestPageResult {
    pageIndex: number;
    isBeforePage: boolean;
    isAfterPage: boolean;
}

export class DocumentComponent {
    static readonly UnusedPageIndex = -1;

    documentElement: HTMLElement;
    documentPagesElement: HTMLElement;
    documentCaretElement: HTMLElement;

    pageComponents: PageComponent[];
    // Now unused, will be used in future
    pageComponentInfos: Map<PageComponent, PageComponentInfo>;
    numberOfPageComponents = 4;

    pageComponentsByPageContentRoot = new Map<Node, PageComponent>();
    caretPageComponent: PageComponent | null;

    quillMirrors: QuillMirror[];
    updateLayoutPageDomObserver: DomObserver;

    pageMargin: TRBL;
    pagePadding: TRBL;

    pageWidth: number = 0;
    pageHeight: number = 0;

    beforeFirstPageSpacing: number = 20;
    betweenPagesSpacing: number = 40;
    afterLastPageSpacing: number = 20;

    pagesLength: number = 0;

    scrollbar: Scrollbar;

    destroy$ = new Subject<void>();

    constructor() {
        this.init();
    }

    init() {
        // Elements

        const html = '<div class="document"><div class="document-pages"><div class="document-caret"></div></div></div>';
        this.documentElement = domService.parseHtml(html)[0] as HTMLElement;
        this.documentPagesElement = this.documentElement.querySelector('.document-pages') as HTMLElement;
        this.documentCaretElement = this.documentElement.querySelector('.document-caret') as HTMLElement;

        // Page components

        this.pageComponents = [];
        this.pageComponentInfos = new Map();

        for (let i = 0; i < this.numberOfPageComponents; i++) {
            const pageComponent = new PageComponent();
            pageComponent.pageContainerClasses.set('document-page-container');
            pageComponent.addTo(this.documentPagesElement);
            this.pageComponents.push(pageComponent);

            this.pageComponentInfos.set(pageComponent, {
                pinMode: null,
                isVisible: false
            });
        }

        // Pages sync

        this.quillMirrors = [];

        for (let i = 0; i < this.numberOfPageComponents; i++) {
            const syncTargetPageComponents = this.pageComponents.filter((pageComponent, index) => i !== index);

            this.quillMirrors.push(new QuillMirror(this.pageComponents[i], syncTargetPageComponents));
        }

        // Update layout

        this.updateLayoutPageDomObserver = new DomObserver(
            { target: this.pageComponents[0].pagesElement, options: DomObserver.MutationObserverInit_All },
            PageComponent.DomObserverOptions,
            null,
            () => {
                this.updateLayout();
            }
        );

        // Page length updating

        this.pageComponents[0].pagesLength$
            .pipe(takeUntil(this.destroy$))
            .subscribe(
                value => {
                    if (this.pagesLength !== value) {
                        this.pagesLength = value;
                        this.updateLayout();
                    }
                }
            );

        // Selection change

        //document.addEventListener('selectionchange', this.onSelectionChange);

        // Scroll

        this.scrollbar = SmoothScrollbar.init(this.documentElement, {
            alwaysShowTracks: true
        });
        this.scrollbar.addListener(this.onScroll);
    }

    destroy() {
        this.remove();

        this.destroy$.next();
        this.destroy$.complete();

        // Page components

        for (const pageComponent of this.pageComponents) {
            pageComponent.destroy();
        }

        // Pages sync

        for (const m of this.quillMirrors) {
            m.destroy();
        }

        // Update layout

        this.updateLayoutPageDomObserver.destroy();

        // Selection change

        //document.removeEventListener('selectionchange', this.onSelectionChange);

        for (const pageComponent of this.pageComponents) {
            pageComponent.quill.off('text-change', this.onSelectionChange);
            pageComponent.quill.off('selection-change', this.onSelectionChange);
        }

        // Scroll

        this.scrollbar.removeListener(this.onScroll);
        SmoothScrollbar.destroy(this.documentElement);
    }

    addTo(parent: HTMLElement) {
        domService.addHtmlTo(this.documentElement, parent);
        this.onAddToDom();
    }

    onAddToDom() {
        for (const pageComponent of this.pageComponents) {
            pageComponent.onAddToDom();
        }
        for (let i = 0; i < this.numberOfPageComponents; i++) {
            this.pageComponentsByPageContentRoot.set(this.pageComponents[i].pagesPlaceholderElement, this.pageComponents[i]);
        }
        for (const m of this.quillMirrors) {
            m.onAddToDom();
        }
        for (const pageComponent of this.pageComponents) {
            pageComponent.quill.on('text-change', this.onSelectionChange);
            pageComponent.quill.on('selection-change', this.onSelectionChange);
        }
        this.updateLayout();
    }

    remove() {
        if (this.documentElement.parentNode) {
            this.documentElement.remove();
        }
    }

    onScroll = () => {
        this.updateLayout();
    };

    getCaretRect(selection: Selection): SelectionRect | null {
        if (!selection.isCollapsed || !selection.anchorNode) {
            return null;
        }

        let result: SelectionRect;

        let caretRect = selection.getRangeAt(0).getBoundingClientRect();
        if (
            caretRect.top === 0
            && caretRect.bottom === 0
            && caretRect.left === 0
            && caretRect.right === 0
        ) {
            const caretReplacementElement = domService.getClosestElement(selection.anchorNode!);
            if (!caretReplacementElement) {
                throw new Error();
            }

            caretRect = caretReplacementElement.getBoundingClientRect();

            result = {
                left: caretRect.left,
                top: caretRect.top,
                width: 0,
                height: caretRect.height
            };
        } else {
            result = {
                left: caretRect.left,
                top: caretRect.top,
                width: caretRect.width,
                height: caretRect.height
            };
        }

        return result;
    }

    scrollToCaret() {
        const selection = window.getSelection();

        const data = this.getPageComponentByCollapsedSelection(selection);
        if (!data) {
            return;
        }
        const { pageComponent, pageIndex, caretRect } = data;
        if (pageIndex !== pageComponent.pageIndex) {
            return;
        }

        const pagesRect = this.documentPagesElement.getBoundingClientRect();
        const caretMargin: { horizontal: number; vertical: number } = { horizontal: 20, vertical: 20 };
        const caretScrollRect = {
            left: caretRect.left - pagesRect.left - caretMargin.horizontal,
            top: caretRect.top - pagesRect.top - caretMargin.vertical,
            width: caretRect.width + (2 * caretMargin.horizontal),
            height: caretRect.height + (2 * caretMargin.vertical)
        };
        this.documentCaretElement.style.left = this.sizeInPx(caretScrollRect.left);
        this.documentCaretElement.style.top = this.sizeInPx(caretScrollRect.top);
        this.documentCaretElement.style.width = this.sizeInPx(caretScrollRect.width);
        this.documentCaretElement.style.height = this.sizeInPx(caretScrollRect.height);

        const scrollContent: HTMLElement = Array.prototype.find.call(this.documentElement.children, (elem: Element) => elem.classList.contains('scroll-content'));
        if (-(scrollContent.getBoundingClientRect().top - this.documentElement.getBoundingClientRect().top) !== this.scrollbar.scrollTop) {
            setTimeout(() => {
                this.scrollToCaret();
            });
        } else {
            if (caretScrollRect.top < this.scrollbar.scrollTop) {
                this.scrollbar.scrollTo(this.scrollbar.scrollLeft, caretScrollRect.top);
            } else if (caretScrollRect.top + caretScrollRect.height > this.scrollbar.scrollTop + this.scrollbar.size.container.height) {
                this.scrollbar.scrollTo(this.scrollbar.scrollLeft, caretScrollRect.top + caretScrollRect.height - this.scrollbar.size.container.height);
            }
        }
    }

    getFocusPageComponent(): PageComponent | null {
        const selection = window.getSelection();
        if (!selection || !selection.anchorNode) {
            return null;
        }

        const pageComponent = this.getPageComponentByNode(selection.anchorNode);
        return pageComponent;
    }

    exec(command: string) {
        const pageComponent = this.getFocusPageComponent();
        if (!pageComponent) {
            return;
        }

        const button = pageComponent.pageContainerElement.querySelector('.ql-' + command) as HTMLButtonElement;
        button.click();
    }

    onSelectionChange = () => {
        setTimeout(
            () => this.onSelectionChangeBody()
        );
    };

    onSelectionChangeBody() {
        setTimeout(() => {
            this.scrollToCaret();
        });

        if ((window as any)._t) {
            debugger
        }

        const selection = window.getSelection();

        const data = this.getPageComponentByCollapsedSelection(selection);
        if (!data) {
            return;
        }
        const { pageComponent, pageIndex, caretRect } = data;
        if (pageIndex === pageComponent.pageIndex) {
            return;
        }
        const newCaretPageIndex = pageIndex;

        const selectionAnchorNodeAddress = pageComponent.getNodeAddress(selection!.anchorNode!)!;

        let newCaretPageComponent = this.getPageComponentByPageIndex(newCaretPageIndex);
        if (!newCaretPageComponent) {
            newCaretPageComponent = this.getUnusedPageComponent();
            if (!newCaretPageComponent) {
                throw new Error();
            }
        }
        this.setCaretPageComponent(newCaretPageComponent, newCaretPageIndex);
        setTimeout(() => {
            this.scrollbar.update();
        });

        const newSelectionAnchorNode = newCaretPageComponent.getNodeByAddress(selectionAnchorNodeAddress)!;
        selection!.setBaseAndExtent(newSelectionAnchorNode, selection!.anchorOffset, newSelectionAnchorNode, selection!.focusOffset);
    };

    getPageComponentByPageIndex(pageIndex: number): PageComponent | null {
        const filteredPageComponents = this.pageComponents.filter(pageComponent => pageComponent.pageIndex === pageIndex && !pageComponent.isPageIndexUnset);
        return filteredPageComponents.length ? filteredPageComponents[0] : null;
    }

    getPageComponentByNode(node: Node): PageComponent | null {
        let pageComponent: PageComponent | null = null;
        let curNode: Node | null = node;

        do {
            const candidatePageComponent = this.pageComponentsByPageContentRoot.get(curNode);
            if (candidatePageComponent) {
                pageComponent = candidatePageComponent;
                break;
            }
        } while (!!(curNode = curNode.parentNode));

        return pageComponent;
    }

    getPageComponentByCollapsedSelection(selection: Selection | null): {
        pageComponent: PageComponent,
        pageIndex: number,
        caretRect: SelectionRect
    } | null {
        if (!selection) {
            return null;
        }

        if (!selection.isCollapsed) {
            return null;
        }

        if (!selection.anchorNode) {
            return null;
        }

        let pageComponent: PageComponent | null = null;
        let curNode: Node | null = selection.anchorNode;

        do {
            const candidatePageComponent = this.pageComponentsByPageContentRoot.get(curNode);
            if (candidatePageComponent) {
                pageComponent = candidatePageComponent;
                break;
            }
        } while (!!(curNode = curNode.parentNode));

        if (!pageComponent) {
            return null;
        }

        const caretRect = this.getCaretRect(selection)!;

        const pageContentRootRect = pageComponent.columnsElement.getBoundingClientRect();
        const caretPositionOnPages: Position = {
            left: caretRect.left + pageComponent.columnsElement.scrollLeft - pageContentRootRect.left,
            top: caretRect.top - pageContentRootRect.top
        };

        // [0; pageWidth - 1]
        const pageIndex = Math.floor(caretPositionOnPages.left / (this.pageWidth - this.pagePadding.left - this.pagePadding.right));

        return {
            pageComponent,
            pageIndex,
            caretRect
        };
    }

    getUnusedPageComponents(): PageComponent[] {
        return this.pageComponents.filter(pageComponent => pageComponent.isPageIndexUnset);
    }

    getUnusedPageComponent(): PageComponent | null {
        const pageComponents = this.getUnusedPageComponents();
        return pageComponents.length ? pageComponents[0] : null;
    }

    setPageMarginLeftRight(left: number, right: number) {
        this.pageMargin = {
            top: 0,
            right,
            bottom: 0,
            left
        };

        for (const pageComponent of this.pageComponents) {
            pageComponent.setPageMargin(this.pageMargin);
        }
    }

    setPageSize(width: number, height: number) {
        this.pageWidth = width;
        this.pageHeight = height;

        for (const pageComponent of this.pageComponents) {
            pageComponent.setPageSize(width, height);
        }
    }

    setPagePadding(padding: TRBL) {
        this.pagePadding = Object.assign({}, padding);

        for (const pageComponent of this.pageComponents) {
            pageComponent.setPagePadding(this.pagePadding);
        }
    }

    setDocumentContent(html: Html) {
        for (const pageComponent of this.pageComponents) {
            pageComponent.setDocumentContent(html);
        }
    }

    updateLayout() {
        this.documentPagesElement.style.height = this.sizeInPx(
            this.getPagesPanelHeight()
        );

        for (const pageComponent of this.pageComponents) {
            pageComponent.updateLayout();
        }

        this.updatePagePositionsAndContents();
    }

    updatePagePositionsAndContents() {
        const visiblePageIndices = this.getVisiblePageIndices();

        let pageComponentIndex = 0;
        for (let i = 0; i < visiblePageIndices.length; i++) {
            const pageIndex = visiblePageIndices[i];

            const pinnedPageComponent = this.getPinnedPageComponentByPageIndex(pageIndex);
            if (pinnedPageComponent) {
                continue;
            }

            let pageComponent: PageComponent | null = null;
            while (
                pageComponentIndex < this.pageComponents.length
                && this.isPageComponentPinned(pageComponent = this.pageComponents[pageComponentIndex++])
            ) {}

            if (!pageComponent) {
                throw new Error();
            }

            this.setPageComponentPageIndex(pageComponent, pageIndex);
            this.pageComponentInfos.get(pageComponent)!.isVisible = true;
        }

        for (let i = pageComponentIndex; i < this.pageComponents.length; i++) {
            const pageComponent = this.pageComponents[i];
            if (this.isPageComponentPinned(pageComponent)) {
                continue;
            }

            this.setPageComponentPageIndex(pageComponent, DocumentComponent.UnusedPageIndex);
            this.pageComponentInfos.get(pageComponent)!.isVisible = false;
        }

        for (const pageComponent of this.pageComponents) {
            if (this.isPageComponentPinned(pageComponent)) {
                this.setPageComponentPageIndex(pageComponent, pageComponent.pageIndex, 'position');
                this.pageComponentInfos.get(pageComponent)!.isVisible = visiblePageIndices.includes(pageComponent.pageIndex);
            }
        }
    }

    isPageComponentPinned(pageComponent: PageComponent): boolean {
        return !!(this.pageComponentInfos.get(pageComponent)!.pinMode);
    }

    getPinnedPageComponentByPageIndex(pageIndex: number): PageComponent | null {
        if (this.caretPageComponent && this.caretPageComponent.pageIndex === pageIndex) {
            return this.caretPageComponent;
        }

        return null;
    }

    getPagesPanelHeight() {
        if (this.pagesLength <= 0) {
            return 0;
        }

        return this.beforeFirstPageSpacing
            + ((this.pagesLength - 1) * this.betweenPagesSpacing)
            + this.afterLastPageSpacing
            + (this.pagesLength * this.pageHeight);
    }

    getPageTopInDocument(pageIndex: number) {
        return this.beforeFirstPageSpacing
            + (pageIndex * this.pageHeight)
            + ((pageIndex === 0) ? 0 : (pageIndex * this.betweenPagesSpacing));
    }

    getVisiblePageIndices(): number[] {
        let result: number[];

        const visibleAreaTop = this.scrollbar.scrollTop;
        const visibleAreaBottom = visibleAreaTop + this.scrollbar.size.container.height;

        const topNearestPage = this.getNearestPage(visibleAreaTop);
        const bottomNearestPage = this.getNearestPage(visibleAreaBottom);

        if (!topNearestPage || !bottomNearestPage) {
            result = [];
        } else if (
            topNearestPage.pageIndex === bottomNearestPage.pageIndex
            && (topNearestPage.isBeforePage || topNearestPage.isAfterPage)
            && !!topNearestPage.isBeforePage === !!bottomNearestPage.isBeforePage
            && !!topNearestPage.isAfterPage === !!bottomNearestPage.isAfterPage
        ) {
            result = [];
        } else {
            const topPageIndex =
                topNearestPage.isBeforePage
                    ? topNearestPage.pageIndex
                    : topNearestPage.isAfterPage
                        ? Math.min(topNearestPage.pageIndex + 1, this.pagesLength - 1)
                        : topNearestPage.pageIndex;

            const bottomPageIndex =
                bottomNearestPage.isBeforePage
                    ? Math.max(bottomNearestPage.pageIndex - 1, 0)
                    : bottomNearestPage.isAfterPage
                        ? bottomNearestPage.pageIndex
                        : bottomNearestPage.pageIndex;

            result = [];
            for (let pageIndex = topPageIndex; pageIndex <= bottomPageIndex; pageIndex++) {
                result.push(pageIndex);
            }
        }

        let afterLastPageIndex: number = -1;
        if (result.length) {
            afterLastPageIndex = result[result.length - 1] + 1;
        } else if (bottomNearestPage) {
            if (bottomNearestPage.isBeforePage) {
                afterLastPageIndex = bottomNearestPage.pageIndex;
            } else {
                afterLastPageIndex = bottomNearestPage.pageIndex + 1;
            }
        }
        if (afterLastPageIndex >= 0 && this.pagesLength > 0 && afterLastPageIndex < this.pagesLength) {
            result.push(afterLastPageIndex);
        }

        return result;
    }

    isPageComponentUsedByInfo(pageComponentInfo: PageComponentInfo) {
        return pageComponentInfo.pinMode || pageComponentInfo.isVisible;
    }

    isPageComponentUsed(pageComponent: PageComponent) {
        return this.isPageComponentUsedByInfo(this.pageComponentInfos.get(pageComponent)!);
    }

    mountPageComponent(pageComponent: PageComponent, pageIndex: number) {
        this.setPageComponentPageIndex(pageComponent, pageIndex);
    }

    unmountPageComponent(pageComponent: PageComponent) {
        this.setPageComponentPageIndex(pageComponent, DocumentComponent.UnusedPageIndex);
    }

    setCaretPageComponent(pageComponent: PageComponent | null, pageIndex: number | null) {
        if (this.caretPageComponent) {
            const oldInfo = this.pageComponentInfos.get(this.caretPageComponent)!;
            oldInfo.pinMode = null;
            if (!this.isPageComponentUsedByInfo(oldInfo)) {
                this.unmountPageComponent(this.caretPageComponent);
            }
        }

        this.caretPageComponent = pageComponent;

        if (this.caretPageComponent) {
            const newInfo = this.pageComponentInfos.get(this.caretPageComponent)!;
            newInfo.pinMode = PageComponentPinMode.hasCaret;
            this.mountPageComponent(this.caretPageComponent, pageIndex!);
        }
    }

    setPageComponentPageIndex(pageComponent: PageComponent, pageIndex: number, mode: 'all' | 'content' | 'position' = 'all') {
        if (pageIndex === DocumentComponent.UnusedPageIndex) {
            if (['all', 'position'].includes(mode)) {
                pageComponent.pageContainerStyles.set({
                    top: this.sizeInPx(-2 * this.pageHeight)
                    //top: this.sizeInPx(20),
                    //left: this.sizeInPx(200)
                });
            }

            if (['all', 'content'].includes(mode)) {
                pageComponent.setPageIndex(PageComponent.UnsetPageIndex);
            }
        } else {
            if (['all', 'position'].includes(mode)) {
                pageComponent.pageContainerStyles.set({
                    top: this.sizeInPx(this.getPageTopInDocument(pageIndex))
                });
            }

            if (['all', 'content'].includes(mode)) {
                pageComponent.setPageIndex(pageIndex);
            }
        }
    }

    getNearestPage(yInDocument: number): GetNearestPageResult | null {
        let result: GetNearestPageResult | null;

        if (this.pagesLength === 0) {
            result = null;
        } else if (yInDocument < this.beforeFirstPageSpacing) {
            result = {
                pageIndex: 0,
                isBeforePage: true,
                isAfterPage: false
            };
        } else {
            const fractionDenominator = this.pageHeight + this.betweenPagesSpacing;

            const fractionedY = yInDocument / fractionDenominator;
            const fractionedYInteger = Math.floor(fractionedY);

            if (fractionedYInteger > this.pagesLength - 1) {
                result = {
                    pageIndex: this.pagesLength - 1,
                    isBeforePage: false,
                    isAfterPage: true
                };
            } else {
                const yModulo = yInDocument % fractionDenominator;

                if (yModulo < this.pageHeight) {
                    result = {
                        pageIndex: fractionedYInteger,
                        isBeforePage: false,
                        isAfterPage: false
                    };
                } else if (yModulo < this.pageHeight + (this.betweenPagesSpacing / 2)) {
                    result = {
                        pageIndex: fractionedYInteger,
                        isBeforePage: false,
                        isAfterPage: true
                    };
                } else {
                    result = {
                        pageIndex: fractionedYInteger + 1,
                        isBeforePage: true,
                        isAfterPage: false
                    };
                }
            }
        }

        return result;
    }

    sizeInPx(size: number): string {
        return `${size}px`;
    }
}