import { assertService } from "../modules/text-document/services/AssertService";

export class ElementClasses {
    classes: string[];

    constructor(
        public element: HTMLElement
    ) {
        assertService.assert(!!element);
    }

    set(...classes: string[]) {
        if (this.classes?.length) {
            this.element.classList.remove(...this.classes);
        }

        if (classes?.length) {
            this.element.classList.add(...classes);
        }

        this.classes = classes;
    }
}