import { domService } from "../modules/text-document/services/DomService";
import { Html } from "../modules/text-document/models/Model";
import { DomObserver, DomObserverOptions } from "./DomObserver";
import { ElementClasses } from "./ElementClasses";
import { TRBL, ZeroTRBL } from "./BaseTypes";
import { BehaviorSubject } from 'rxjs';
import { assertService } from "../modules/text-document/services/AssertService";
import { ElementStyles } from "./ElementStyles";
import { DomMirror, DomMirrorHiddenDataOptions } from "./DomMirror";
import { pageComponentIdGenerator } from "./ElementIdGenerator";
import { getTinymce } from "./TinyMCE";
import Quill from "quill";

export class PageComponent {
    static readonly UnsetPageIndex = -1;
    static readonly HiddenDataContainerClass = 'page-hidden-data__7ec44e0c-94b2-43a2-8ba0-36fa0066e820';
    static readonly ContentClass = "ql-editor";
    static readonly DomObserverOptions: DomObserverOptions = {
        filterMutationsTargetClass: PageComponent.ContentClass,
        ignoreFilterMutationsTargetAttributes: true
    };
    static readonly HiddenDataOptions: DomMirrorHiddenDataOptions = {
        hiddenDataContainerClass: PageComponent.HiddenDataContainerClass,
        ignoreIfHiddenDataHasClass: DomMirror.IgnorePagesSyncHiddenDataClass
    };

    pageContainerElement: HTMLElement;
    pageElement: HTMLElement;
    pageLimiterElement: HTMLElement;
    pagesElement: HTMLElement;
    pageHiddenDataElement: HTMLElement;

    _columnsElement: HTMLElement;
    get columnsElement(): HTMLElement {
        return this._columnsElement;
    }
    set columnsElement(value: HTMLElement) {
        this.columnsElement && this.unsubscribeColumnsScroll();
        this._columnsElement = value;
        if (this.columnsElement) {
            this.subscribeColumnsScroll();
        }
    }

    _pagesPlaceholderElement: HTMLElement;
    get pagesPlaceholderElement(): HTMLElement {
        return this._pagesPlaceholderElement;
    }
    set pagesPlaceholderElement(value: HTMLElement) {
        this._pagesPlaceholderElement = value;
        if (this.pagesPlaceholderElement) {
            this.setDocumentContent(this.html);
        }
    }

    pageContainerClasses: ElementClasses;
    pageContainerStyles: ElementStyles;

    id = pageComponentIdGenerator.getNewId();
    domId = `page-component-${this.id}`;

    pageWidth: number;
    pageHeight: number;

    pageMargin: TRBL = ZeroTRBL;
    pagePadding: TRBL = ZeroTRBL;

    observer: DomObserver = null as any;

    pageIndex: number = null as any;
    isPageIndexUnset: boolean = false;

    pagesLengthSubject = new BehaviorSubject<number>(0);
    pagesLength$ = this.pagesLengthSubject.asObservable();

    html: Html;

    quill: Quill;

    constructor() {
        const html = `<div id="${this.domId}" class="page-container"><div class="page"><div class="page-limiter"><div class="page-pages"><div class="${PageComponent.HiddenDataContainerClass}"></div><div class="page-froala-placeholder"></div></div></div></div></div>`;
        this.pageContainerElement = domService.parseHtml(html)[0] as HTMLElement;
        this.pageElement = this.pageContainerElement.querySelector('.page') as HTMLElement;
        this.pageLimiterElement = this.pageContainerElement.querySelector('.page-limiter') as HTMLElement;
        this.pagesElement = this.pageContainerElement.querySelector('.page-pages') as HTMLElement;
        this.pageHiddenDataElement = this.pageContainerElement.querySelector(`.${PageComponent.HiddenDataContainerClass}`) as HTMLElement;

        this.pageContainerClasses = new ElementClasses(this.pageContainerElement);
        this.pageContainerStyles = new ElementStyles(this.pageContainerElement);

        this.setPageIndex(PageComponent.UnsetPageIndex);

        this.init();
    }

    init() {
        this.initObserver();

        this.pageElement.addEventListener('click', this.onClick);
    }

    destroy() {
        this.remove();

        this.destroyObserver();

        this.pageElement.removeEventListener('click', this.onClick);

        this.columnsElement && this.unsubscribeColumnsScroll();
    }

    initObserver() {
        const callback = () => {
            this.applyPageIndex();

            this.updatePagesLength();
        };

        this.observer = new DomObserver(
            {
                target: this.pagesElement,
                options: DomObserver.MutationObserverInit_All
            },
            PageComponent.DomObserverOptions,
            null,
            callback
        );
    }

    destroyObserver() {
        this.observer.destroy();
    }

    onClick = (e: MouseEvent) => {
        const s = window.getSelection();
        if (!s) {
            return;
        }
        if (s.anchorNode && !s.isCollapsed) {
            return;
        }

        const limiterRect = this.pageLimiterElement.getBoundingClientRect();
        const x = Math.min(Math.max(e.clientX, limiterRect.left), limiterRect.left + limiterRect.width - 1);
        const y = Math.min(Math.max(e.clientY, limiterRect.top), limiterRect.top + limiterRect.height - 1);

        const r = document.caretRangeFromPoint(x, y);
        if (!r) {
            return;
        }

        if (domService.getClosestElement(r.startContainer.parentNode!)?.closest('.ql-editor')) {
            s.removeAllRanges();
            s.addRange(r);
        } else {
            const getLastNode = (parent: Node): Node => {
                if (parent.childNodes?.length) {
                    return getLastNode(parent.lastChild!);
                }
                return parent;
            };

            const lastNode = getLastNode(this.pagesPlaceholderElement);
            const lastNodeParent = lastNode.parentNode!;
            s.setBaseAndExtent(lastNodeParent, lastNodeParent.childNodes.length, lastNodeParent, lastNodeParent.childNodes.length);
        }
    };

    subscribeColumnsScroll() {
        this.columnsElement.addEventListener('scroll', this.onScroll);
    }

    unsubscribeColumnsScroll() {
        this.columnsElement.removeEventListener('scroll', this.onScroll);
    }

    addTo(parent: HTMLElement) {
        domService.addHtmlTo(this.pageContainerElement, parent);
    }

    onAddToDom() {
        const selector = `#${this.domId} .page-froala-placeholder`;

        /*
        new (window as any).FroalaEditor(selector, {
            toolbarInline: true,
            charCounterCount: false,
            toolbarVisibleWithoutSelection: true,
            toolbarButtons: [
                'bold', 'italic', 'underline',

                'insertLink', 'insertImage', 'insertVideo', 'insertTable'

                //
                //
                //

                //'bold', 'italic', 'underline', 'strikeThrough', 'subscript', 'superscript', 'fontFamily', 'fontSize', 'textColor', 'backgroundColor', 'inlineClass', 'inlineStyle', 'clearFormatting',

                //'alignLeft', 'alignCenter', 'formatOLSimple', 'alignRight', 'alignJustify', 'formatOL', 'formatUL', 'paragraphFormat', 'paragraphStyle', 'lineHeight', 'outdent', 'indent', 'quote',

                //'insertLink', 'insertImage', 'insertVideo', 'insertTable', 'emoticons', 'fontAwesome', 'specialCharacters', 'embedly', 'insertFile', 'insertHR',

                //'undo', 'redo', 'fullscreen', 'print', 'getPDF', 'spellChecker', 'selectAll', 'html', 'help'
            ]
        });
        this.columnsElement = this.pageContainerElement.querySelector('.page-froala-placeholder > div') as HTMLElement;
        this.pagesPlaceholderElement = this.pageContainerElement.querySelector('.page-froala-placeholder > div > div') as HTMLElement;
        */

        /*
        const tinymceConfig = {
            selector,
            menubar: false,
            inline: true,
            plugins: [
                'link',
                'lists',
                'autolink',
                'table'
            ],
            toolbar: [
                'bold italic underline | forecolor backcolor | alignleft aligncenter alignright alignjustify | numlist bullist outdent indent',
                'table tabledelete | tableprops tablerowprops tablecellprops | tableinsertrowbefore tableinsertrowafter tabledeleterow | tableinsertcolbefore tableinsertcolafter tabledeletecol'
            ],
            valid_elements: '*[*]',
            valid_styles: {
                '*': 'font-size,font-family,color,text-decoration,text-align'
            },
            powerpaste_word_import: 'clean',
            powerpaste_html_import: 'clean'
        };
        const tinymce = getTinymce();
        if (!tinymce) {
            throw new Error();
        }
        tinymce.init(tinymceConfig);
        //setTimeout(() => {
            this.columnsElement = this.pageContainerElement.querySelector('.page-pages') as HTMLElement;
            this.pagesPlaceholderElement = this.pageContainerElement.querySelector('.page-froala-placeholder') as HTMLElement;
        //});
        */

        this.quill = new (window as any).Quill(selector, {
            theme: 'bubble',
            modules: {
                toolbar: ['bold', 'italic', 'underline', 'strike', 'image'],
                table: true,
                /*
                'better-table': {
                    operationMenu: {
                        color: {
                            colors: ['green', 'red', 'yellow', 'blue', 'white'],
                            text: 'Background Colors:'
                        }
                    }
                }
                */
            }
        });

        this.columnsElement = this.pageContainerElement.querySelector('.page-pages') as HTMLElement;
        this.pagesPlaceholderElement = this.pageContainerElement.querySelector('.ql-editor') as HTMLElement;

        this.updateLayout();
    }

    remove() {
        if (this.pageContainerElement.parentNode) {
            this.pageContainerElement.remove();
        }
    }

    updateLayout() {
        const pageWidthWithMargin = this.pageWidth + this.pageMargin.left + this.pageMargin.right;
        const pageHeightWithMargin = this.pageHeight + this.pageMargin.top + this.pageMargin.bottom;
        const pageWidthWithoutPadding = this.pageWidth - this.pagePadding.left - this.pagePadding.right;
        const pageHeightWithoutPadding = this.pageHeight - this.pagePadding.top - this.pagePadding.bottom;

        this.pageContainerElement.style.paddingTop = this.sizeInPx(this.pageMargin.top);
        this.pageContainerElement.style.paddingRight = this.sizeInPx(this.pageMargin.right);
        this.pageContainerElement.style.paddingBottom = this.sizeInPx(this.pageMargin.bottom);
        this.pageContainerElement.style.paddingLeft = this.sizeInPx(this.pageMargin.left);
        this.pageContainerElement.style.width = this.sizeInPx(pageWidthWithMargin);
        this.pageContainerElement.style.height = this.sizeInPx(pageHeightWithMargin);

        this.pageElement.style.paddingTop = this.sizeInPx(this.pagePadding.top);
        this.pageElement.style.paddingRight = this.sizeInPx(this.pagePadding.right);
        this.pageElement.style.paddingBottom = this.sizeInPx(this.pagePadding.bottom);
        this.pageElement.style.paddingLeft = this.sizeInPx(this.pagePadding.left);

        this.pageLimiterElement.style.width = this.sizeInPx(pageWidthWithoutPadding);
        this.pageLimiterElement.style.height = this.sizeInPx(pageHeightWithoutPadding);

        this.pagesElement.style.width = this.sizeInPx(pageWidthWithoutPadding);
        this.pagesElement.style.height = this.sizeInPx(pageHeightWithoutPadding);

        if (this.columnsElement) {
            this.columnsElement.style.height = this.sizeInPx(pageHeightWithoutPadding + (this.columnsElement.offsetHeight - this.columnsElement.clientHeight));
            this.columnsElement.style.columnWidth = this.sizeInPx(pageWidthWithoutPadding);
        }
    }

    setPageMargin(margin: TRBL) {
        this.pageMargin = Object.assign({}, margin);
        this.updateLayout();
    }

    setPageSize(width: number, height: number) {
        this.pageWidth = width;
        this.pageHeight = height;
        this.updateLayout();
    }

    setPagePadding(padding: TRBL) {
        this.pagePadding = Object.assign({}, padding);
        this.updateLayout();
    }

    setDocumentContent(html: Html) {
        this.html = html;
        if (this.pagesPlaceholderElement && html) {
            this.pagesPlaceholderElement.innerHTML = '';
            domService.addHtmlTo(html, this.pagesPlaceholderElement);
        }
    }

    setPageIndex(pageIndex: number) {
        if (pageIndex === PageComponent.UnsetPageIndex) {
            this.pageIndex = 0;
            this.isPageIndexUnset = true;
            this.applyPageIndex();
        } else {
            this.pageIndex = pageIndex;
            this.isPageIndexUnset = false;
            this.applyPageIndex();
        }
    }

    applyPageIndex() {
        if (this.columnsElement) {
            this.columnsElement.scrollLeft = this.pageIndex * (this.pageWidth - this.pagePadding.left - this.pagePadding.right);
        }
    }

    updatePagesLength() {
        if (this.columnsElement) {
            const pagesLength = this.columnsElement.scrollWidth / (this.pageWidth - this.pagePadding.left - this.pagePadding.right);
            assertService.assert(pagesLength === Math.round(pagesLength));

            this.pagesLengthSubject.next(pagesLength);
        }
    }

    onScroll = () => {
        if (this.columnsElement.scrollLeft !== this.pageIndex * (this.pageWidth - this.pagePadding.left - this.pagePadding.right)) {
            this.columnsElement.scrollLeft = this.pageIndex * (this.pageWidth - this.pagePadding.left - this.pagePadding.right);
        }
    };

    sizeInPx(size: number): string {
        return `${size}px`;
    }

    getNodeAddress(node: Node) {
        return domService.getAddressForNodeRelativeToRoot(node, this.pagesElement);
    }

    getNodeByAddress(address: number[]): Node | null {
        return domService.getNodeByAddressRelativeToRoot(address, this.pagesElement);
    }
}