import Quill, { Sources, TextChangeHandler } from "quill";
import { PageComponent } from "./PageComponent";

export class QuillMirror {
    constructor(
        public fromPageComponent: PageComponent,
        public toPageComponents: PageComponent[]
    ) {}

    init() {
        this.fromPageComponent.quill.on('text-change', this.onTextChange);
    }

    destroy() {
        this.fromPageComponent.quill && this.fromPageComponent.quill.off('text-change', this.onTextChange);
    }

    onAddToDom() {
        this.init();
    }

    onTextChange: TextChangeHandler = (delta: any, oldContents: any, source: Sources) => {
        if (source === 'user') {
            for (const toPageComponent of this.toPageComponents) {
                toPageComponent.quill.updateContents(delta, 'api');
            }
        }
    };
}